.profile__name {
  color: #ffffff;
  font-size: 42px;
  font-weight: 500;
  line-height: 48px;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 620px) {
  .profile__name {
    text-align: center;
    font-size: 27px;
    line-height: 33px;
  }
}